<template>
    <div style="padding-top: 5px;">
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="操作人" prop="user_name">
                <el-input v-model="searchFormData.user_name" placeholder="输入操作人名称"></el-input>
            </el-form-item>
            <el-form-item label="发生时间" prop="处理时间">
                <el-date-picker
                        v-model="searchFormData.send_time"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始时间"
                        end-placeholder="结束时间">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="消息内容" prop="msg">
                <el-input v-model="searchFormData.msg" placeholder="输入消息检索字"></el-input>
            </el-form-item>
            <el-form-item label="消息类型" prop="msg_type_code_id">
                <el-select v-model="searchFormData.msg_type_code_id" placeholder="消息类型" clearable>
                    <el-option v-for="item in msg_type_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="消息等级" prop="msg_level_code_id">
                <el-select v-model="searchFormData.msg_level_code_id" placeholder="消息类型" clearable>
                    <el-option v-for="item in msg_level_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>

            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[18, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper">
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" size="small" :height="getTableHeight(98)">
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60">
            </el-table-column>
            <el-table-column
                    prop="send_time"
                    label="发生时间"
                    width="156">
            </el-table-column>
            <el-table-column
                    prop="createTime"
                    label="处理时间"
                    width="156">
            </el-table-column>
            <el-table-column
                    prop="user_name"
                    label="操作人"
                    width="120">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true"
                             prop="msg"
                             label="消息">
            </el-table-column>
            <el-table-column
                    prop="msg_type_code_id"
                    label="消息类型"
                    width="200"
                    :formatter="format_msg_type">
            </el-table-column>
            <el-table-column
                    prop="msg_level_code_id"
                    label="消息等级"
                    width="100"
                    :formatter="format_msg_level">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true"
                             prop="par"
                             label="参数"
                             width="400">
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'home',
        data() {
            return {
                msg_type_list: null,
                msg_level_list: null,
                searchFormData: {
                    user_name: '',
                    msg: '',
                    msg_type_code_id: '',
                    msg_level_code_id: '',
                    send_time: '',
                    pageNum: 1,
                    pageSize: 18,
                    pageCount: 1,
                    order: '-send_time'
                },
                tableData: []
            }
        },
        async mounted() {
            this.msg_type_list = await this.$setCodeList(this.msg_type_list, 'msg_type');
            this.msg_level_list = await this.$setCodeList(this.msg_level_list, 'msg_level');
            this.search();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置按钮
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            search() {
                let searchData = this.searchFormData;
                this.r_search_msg_info_list(searchData)
            },
            format_msg_type(row, column, cellValue) // 格式化消息类型
            {
                return this.$formatCodeValue(row, this.msg_type_list, 'msg_type_code_id')
            },
            format_msg_level(row, column, cellValue) // 格式化等级类型
            {
                return this.$formatCodeValue(row, this.msg_level_list, 'msg_level_code_id')
            },
            // 数据库操作数据
            r_search_msg_info_list(searchData) {
                let R = new this.$http();
                // let {data} = await R.post(searchData, 'OAMP_search_msg_info_list');
                // 异步模式
                let res = R.post(searchData, 'OAMP_search_msg_info_list');
                res.then(({data}) => {
                    this.searchFormData.pageCount = data.pageCount;
                    this.tableData = data.data
                });
            },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h1 {
        font-weight: normal;
    }

    .item {
        margin: 4px;
    }

</style>



